<template>
	<view class="row">
		<view class="span24-8 px-2 mb-2" v-for="(item,index) in label.list" :key="index">
			<view class="rounded px-2 py-1 bg-light-secondary font-sm text-center border" :class="label.selected === index ? 'radio-active' : ''" @click="change(index)">{{item.name}}</view>
		</view>
	</view>
</template>

<script>
	export default{
		props:{
			label:{
				type:Object,
				default(){
					return{}
				}
			}
		},
		methods:{
			change(index){
				this.$emit('update:selected',index)
			},
		},
	}
</script>

<style scoped>
	.radio-active{
		background-color: #fce0d5 !important;
		border: 1rpx solid #f97417 !important;
	}
</style>
